/* Anno.js by @iamdanfox */

.anno {
    position: absolute;
    padding: 15px;
    z-index: 50000;
    width: 300px;
    cursor: default;
    text-transform: none;
    text-align: left;
    line-height: 0.9em;
    -webkit-transition: all 300ms cubic-bezier(0.68, 0, 0.265, 1);
    -moz-transition: all 300ms cubic-bezier(0.68, 0, 0.265, 1);
    -ms-transition: all 300ms cubic-bezier(0.68, 0, 0.265, 1);
    -o-transition: all 300ms cubic-bezier(0.68, 0, 0.265, 1);
    transition: all 300ms cubic-bezier(0.68, 0, 0.265, 1);
    -webkit-transition-property: opacity, margin;
    -moz-transition-property: opacity, margin;
    transition-property: opacity, margin;
    .anno-inner {
      border: 2px solid $brand-primary__colour;
      position: relative;
      background: white;
      -webkit-box-shadow: 0 5px 20px 0px rgba(0, 0, 0, 0.35);
      -moz-box-shadow: 0 5px 20px 0px rgba(0, 0, 0, 0.35);
      box-shadow: 0 5px 20px 0px rgba(0, 0, 0, 0.35);
      .anno-content {
        padding: 15px 20px;
        line-height: 1.3em;
        font-family: $base__font-family;
        font-weight: normal;
        font-size: 15px;
        color: #56565a;
      }
      .anno-btn-container {
        text-align: right;
        .anno-btn {
          margin: 0 20px 15px -10px;
          padding: 7px 14px;
          height: auto;
          width: auto;
          background: $brand-primary__colour;
          border: none;
          -webkit-border-radius: 0px;
          -moz-border-radius: 0px;
          -ms-border-radius: 0px;
          -o-border-radius: 0px;
          border-radius: 0px;
          font-family: $base__font-family;
          font-weight: bold;
          font-size: 12px;
          color: white;
          -webkit-transition: all 150ms cubic-bezier(0.68, 0, 0.265, 1);
          -moz-transition: all 150ms cubic-bezier(0.68, 0, 0.265, 1);
          -ms-transition: all 150ms cubic-bezier(0.68, 0, 0.265, 1);
          -o-transition: all 150ms cubic-bezier(0.68, 0, 0.265, 1);
          transition: all 150ms cubic-bezier(0.68, 0, 0.265, 1);
          &:hover {
            background: $brand-primary__colour;
            opacity: 0.8;
          }
          &:active {
            background: $brand-primary__colour;
          }
          &:focus {
            outline: $brand-primary__colour;
          }
          &.anno-btn-low-importance {
            background: none;
            color: #bbbbbd;
            &:hover {
              color: #78787b;
            }
          }
        }
      }
    }
    .anno-arrow {
      position: absolute;
      border: 8px solid white;
    }
    &.anno-target-center-bottom .anno-arrow, &.anno-target-bottom .anno-arrow {
      top: 100%;
      left: 20px;
      border-bottom: none;
      border-color: $brand-primary__colour transparent;
    }
    &.anno-target-center-bottom.anno-hidden, &.anno-target-bottom.anno-hidden {
      margin-top: -20px;
    }
    &.anno-target-center-left .anno-arrow, &.anno-target-left .anno-arrow {
      right: 100%;
      top: 15px;
      border-left: none;
      border-color: transparent $brand-primary__colour;
    }
    &.anno-target-center-left.anno-hidden, &.anno-target-left.anno-hidden {
      margin-left: 20px;
    }
    &.anno-target-center-top .anno-arrow, &.anno-target-top .anno-arrow {
      bottom: 100%;
      left: 20px;
      border-top: none;
      border-color: $brand-primary__colour transparent;
    }
    &.anno-target-center-top.anno-hidden, &.anno-target-top.anno-hidden {
      margin-top: 20px;
    }
    &.anno-target-center-right .anno-arrow, &.anno-target-right .anno-arrow {
      left: 100%;
      top: 15px;
      border-right: none;
      border-color: transparent $brand-primary__colour;
    }
    &.anno-target-center-right.anno-hidden, &.anno-target-right.anno-hidden {
      margin-left: -20px;
    }
    &.anno-target-center-left .anno-arrow, &.anno-target-center-right .anno-arrow {
      top: 50%;
      margin-top: -8px;
    }
    &.anno-target-center-bottom .anno-arrow, &.anno-target-center-top .anno-arrow {
      left: 50%;
      margin-left: -8px;
    }
    &.anno-hidden {
      opacity: 0;
    }
    &.anno-width-400 {
      width: 400px;
    }
    &.anno-width-250 {
      width: 250px;
    }
    &.anno-width-200 {
      width: 200px;
    }
    &.anno-width-175 {
      width: 175px;
    }
    &.anno-width-150 {
      width: 150px;
    }
  }
  
  .anno-overlay {
    position: fixed;
    z-index: 1000;
    height: 100%;
    width: 100%;
    top: 0;
    left: 0;
    /*pointer-events:none; // allows clickthrough*/
    background: rgba(0, 0, 0, 0.7);
    -webkit-transition: all 300ms cubic-bezier(0.68, 0, 0.265, 1);
    -moz-transition: all 300ms cubic-bezier(0.68, 0, 0.265, 1);
    -ms-transition: all 300ms cubic-bezier(0.68, 0, 0.265, 1);
    -o-transition: all 300ms cubic-bezier(0.68, 0, 0.265, 1);
    transition: all 300ms cubic-bezier(0.68, 0, 0.265, 1);
    &.anno-hidden {
      pointer-events: none;
      opacity: 0;
    }
  }
  
  .anno-emphasise-target {
    z-index: 1001 !important !important;
  }