/* Backgrounds */
.background-primary { background-color: #ffffff; }

.background-white { background-color: white; }

.background-secondary { background-color: #F5F5F5; }

/* Typography */
body { font-family: "Roboto Condensed", Verdana, Geneva, sans-serif; weight: normal; color: #444444; opacity: 1; }

body h1 { font-family: Rubik, Verdana, Geneva, sans-serif; weight: normal; color: #4A4A4A; opacity: 100%; }

@media (min-width: 768px) { body h1 { font-size: 32px; } }

@media (max-width: 767px) { body h1 { font-size: 21px; } }

body h2, body h3, body h4, body h5 { font-family: Rubik, Verdana, Geneva, sans-serif; weight: 500; color: #444444; opacity: 100%; }

@media (min-width: 768px) { body h2, body h3, body h4, body h5 { font-size: 21px; } }

@media (max-width: 767px) { body h2, body h3, body h4, body h5 { font-size: 18px; } }

body p { text-align: justify; }

@media (min-width: 768px) { body p { font-size: 16px; } }

@media (max-width: 767px) { body p { font-size: 12px; } }

body hr { height: 2px; color: #cccccc; opacity: 0.5; }

body #page-wrapper { margin-top: 60px; display: inline-block; min-height: calc(100vh - 60px); width: 100%; }

@media (max-width: 767px) { body #page-wrapper { margin-top: 60px; } }

body #login-wrapper { min-height: 100vh; width: 100%; }

