/* Button Styling */
.button-text-width-limit { max-width: 100px; }

.button-primary { border: 1.5px solid; border-color: #CCCCCC; border-radius: 4px; color: white; background-color: #CCCCCC; font-size: 14px; font-weight: 500; line-height: 15px; text-align: center; padding: 5px; display: inline-block; }

.button-primary:hover { opacity: 0.7; text-decoration: none; }

.button-secondary { border: 1.5px solid; border-color: #CCCCCC; border-radius: 4px; background-color: #FFFFFF; font-size: 14px; font-weight: 500; line-height: 15px; text-align: center; padding: 5px; display: inline-block; }

.button-secondary:hover { opacity: 0.7; text-decoration: none; }

.button-secondary.collapsed svg { -webkit-transform: rotate(180deg); -ms-transform: rotate(180deg); transform: rotate(180deg); }

.button-tertiary { border-color: #918F8F; background-color: #ffffff; color: #918F8F; }

.button-tertiary-active { background-color: #918F8F; color: #ffffff; }

/* Misc Styling */
.text-tertiary { color: #918F8F; }

img { max-width: 100%; margin: auto; height: auto; }

